-bar__menu button {\n\tbackground-color: transparent;\n\tborder: none;\n\tmin-height: 44px;\n\tmin-width: 44px;\n\tfont-size: 25px;\n\tpadding: 8px;\n\tcursor: pointer;\n}\n\n.app-bar .app-bar__brand {\n\tdisplay: flex;\n\talign-items: center;\n}\n\n.app-bar .app-bar__brand h1 {\n\tcolor: black;\n\ttext-transform: uppercase;\n\tuser-select: none;\n\tfont-size : 2em;\n\tmargin-top: 20px;\n}\n\n.app-bar .app-bar__navigation {\n\tposition: absolute;\n\ttop: 50px;\n\tleft: -180px;\n\twidth: 150px;\n\ttransition: all 0.3s;\n\tpadding: 8px;\n\tbackground-color: white;\n\toverflow: hidden;\n\tmargin-top: 20px;\n}\n\n.app-bar .app-bar__navigation.open {\n\tleft: 0;\n}\n\n.app-bar .app-bar__navigation ul li a {\n\tdisplay: inline-block;\n\ttext-decoration: none;\n\tcolor: black;\n\tpadding: 8px;\n\tmargin-bottom: 5px;\n\twidth: 100%;\n\tmin-height: 44px;\n\tmin-width: 44px;\n}\n\n.shadow-bottom {\n    box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 7px 0px;\n}\n\n.hero {\n\tdisplay: flex;\n\talign-items: center;\n\tmin-height: 380px;\n\twidth: 100%;\n\ttext-align: center;\n\tbackground-position: center;\n\tbackground-color: Black !important;\n\tbackground-image: url(\"../public/images/heros/hero-image_4.jpg\");\n}\n\n.skip-link {\n\tposition: absolute;\n\ttop: -100px;\n\tleft: 5px;\n\tbackground-color: #bf1722;\n\tcolor: white;\n\tpadding: 10px;\n\tfont-size: 20px;\n\tz-index: 100;\n\ttext-decoration: none\n}\n\n.skip-link:focus {\n\ttop: 100px\n}\n\n.heroinner {\n\tmargin: 0 auto;\n\tmax-width: 800px\n}\n\n.herotitle {\n\tcolor: #fff;\n\tfont-weight: 600;\n\tfont-size: 36px;\n\ttext-shadow: 1px 1px #141414\n}\n\n.herosubtitle {\n\tcolor: #fff;\n\tmargin-top: 16px;\n\tfont-size: 18px;\n\tfont-weight: 600;\n\ttext-shadow: 1px 1px #141414\n}\n\nmain {\n\tpadding: 32px;\n  \tflex: 1;\n} \n\n#mainContent {\n  margin: 0 auto;\n  min-height: 100%;\n  max-width: 100%;\n\tpadding: 32px;\n\tbackground-color: ghostwhite;\n}\n\n  /*\n\tfood\n  */\n   \n  #resto-lists {\n\tdisplay: grid;\n\tgrid-template-columns: 1fr;\n\tgap: 16px;\n\tmargin: 32px 0;\n  }\n   \n   \n  /*\n\tfood Item\n  */\n   \n  .food-item {\n\tmin-width: 44px;\n\tmin-width: 44px;\n\twidth: 100%;\n\tbox-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n\tborder-radius: 8px;\n\toverflow: hidden;\n  }\n   \n  .food-item__header {\n\tposition: relative;\n  }\n   \n  .food-item .food-item__header .food-item__header__poster {\n\twidth: 100%;\n    min-height: 100%;\n  }\n   \n  .food-item .food-item__header .food-item__header__rating {\n\tposition: absolute;\n\tpadding: 8px;\n\tbottom: 20px;\n\tleft: 0;\n\tdisplay: inline-block;\n\tbackground-color: black;\n\tcolor: white;\n  }\n   \n  .food-item .food-item__header .food-item__header__rating .food-item__header__rating__score {\n\tmargin-left: 10px;\n  }\n   \n  .food-item .food-item__content {\n\tpadding: 16px;\n  }\n   \n  .food-item .food-item__content h3 {\n\tmargin: 0 0 10px 0;\n  }\n   \n  .food-item .food-item__content h3 a {\n\tcolor: #db0000;\n\ttext-decoration: none;\n\tpadding-top: 44px;\n\tpadding-bottom: 44px;\n  }\n   \n  .food-item .food-item__content p {\n\toverflow: hidden;\n\ttext-overflow: ellipsis;\n\tdisplay: -webkit-box;\n\t-webkit-line-clamp: 4; /* number of lines to show */\n\t-webkit-box-orient: vertical;\n  }\n   \n   \n  /*\n\tfood\n  */\n   \n  #resto-lists {\n\tmargin: 0 auto;\n\twidth: 100%;\n\tmax-width: 1000px;\n\tdisplay: grid;\n\tgrid-template-columns: 1fr;\n\tgap: 18px 16px;\n  }\n   \n  #resto-lists .food__image {\n\twidth: 100%;\n\tmax-width: 300px;\n  }\n   \n  #resto-lists .food__info h4 {\n\tmargin: 8px 0;\n  }\n\n\n.search-box {\n    margin-bottom: 1rem;\n    display: block;\n    width: 100%;\n    padding: 0.375rem 0.75rem;\n    min-height: 44px;\n    font-size: 1rem;\n    font-weight: 400;\n    line-height: 1.5;\n    color: #212529;\n    background-color: #fff;\n    background-clip: padding-box;\n    border: 1px solid blueviolet;\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n