ntent {\n\tpadding: 24px;\n  }\n  \n  .resto__name {\n\tfont-weight: 600;\n\tcolor: black;\n\tpadding: 8px 0 16px;\n\tdisplay: inline-block;\n\ttext-decoration: none;\n\ttransition: color 0.3s;\n}\n.resto__name:hover {\n\tcolor: #000;\n}\n\n  .resto__city,\n  .resto__rating {\n\tposition: absolute;\n\tbottom: 16px;\n\tcolor: white;\n\tpadding: 4px 8px;\n  }\n  \n  .resto__city {\n\tleft: 16px;\n  }\n  \n  .resto__rating {\n\tright: 16px;\n  }\n  \n  .resto__description {\n\tline-height: 1.7;\n\tcolor: black;\n\tfont-size: 14px;\n\toverflow: hidden;\n\ttext-overflow: clip;\n\tdisplay: -webkit-box;\n\t-webkit-line-clamp: 10px;\n\t-webkit-box-orient: vertical;\n}\n\nresto-detail {\n\tdisplay: block;\n}\nresto-detail p {\n\tline-height: 1.7;\n\tfont-size: 14px;\n}\nresto-detail h1 {\n\tfont-size: 1.8em;\n\tcolor: black;\n\tmargin-bottom: 16px;\n}\nresto-detail h2 {\n\tfont-size: 1.4em;\n\tpadding-top: 1em;\n\tcolor: red;\n\tmargin-bottom: 8px;\n}\nresto-detail h3 {\n\tfont-size: 1.15em;\n\tcolor: grey;\n\tmargin-top: 8px;\n\tmargin-bottom: 4px;\n}\n\nresto-detail li {\n\tline-height: 1.5;\n}\n/* --- Resto Info --- */\nresto-info {\n\tdisplay: block;\n}\n.description {\n\tborder: 2px;\n\tborder-radius: 8px;\n\tpadding: 24px;\n\tmargin-bottom: 16px;\n}\n.desc__content {\n\tdisplay: grid;\n\tgap: 16px;\n}\n.resto-img {\n\tposition: relative;\n\tpadding-bottom: 56.2%;\n}\n.resto-img img {\n\tposition: absolute;\n\tobject-fit: cover;\n\twidth: 100%;\n\theight: 100%;\n\tborder-radius: 8px;\n}\n.info {\n\tdisplay: grid;\n\tgap: 16px;\n\tmargin-bottom: 16px;\n}\n.main-info, .resto-menus {\n\tborder: 2px;\n\tborder-radius: 8px;\n\tpadding: 24px;\n}\n.main-info h2 {\n\tmargin-bottom: 16px;\n}\n.menus {\n\tdisplay: grid;\n\tgap: 16px;\n}\n/* --- Resto review --- */\nresto-review {\n\tdisplay: block;\n\tborder: 2px;\n\tborder-radius: 8px;\n\toverflow: hidden;\n\tpadding: 24px;\n}\nresto-review ul {\n\tlist-style-type: none;\n}\n.review_item {\n\tpadding: 8px 0;\n\tdisplay: flex;\n}\n.reviewer_photo {\n\tmargin-right: 8px;\n}\n.review_content, form {\n\tbackground-color: #f7f7f7;\n\tflex: 1;\n\tpadding: 16px;\n\tdisplay: block;\n\tborder-radius: 16px;\n\toverflow: hidden;\n}\ntextarea {\n\tfont-family: verdana;\n}\n.review_name {\n\tfont-weight: 700;\n\tline-height: 1.3;\n}\n.review_date {\n\tfont-size: 0.8em;\n\tcolor: black;\n}\ninput[type=\"text\"], textarea {\n\tpadding: 10px;\n\tmargin: 10px 0;\n\tborder-radius: 8px;\n\tborder: 2px solid #d0d0d0;\n\twidth: 100%;\n\ttransition: box-shadow 0.3s ease-in-out;\n\tfont-size: 0.9em;\n}\ninput[type=\"text\"]:focus, textarea:focus, input[type=\"text\"]:hover, textarea:hover {\n\tbox-shadow: 0 4px 8px 0 #000 33;\n\toutline: none;\n}\nbutton[type=\"submit\"] {\n\tbackground-color: grey;\n\tborder: none;\n\tborder-radius: 8px;\n\tcolor: white;\n\tpadding: 12px 24px;\n\tfont-size: 0.95em;\n\tcursor: pointer;\n\ttransition: box-shadow 0.3s ease-in-out, background-color 0.2s ease-in-out;\n\twidth: 100%;\n\tdisplay: flex;\n\tjustify-content: center;\n}\nbutton[type=\"submit\"]:active {\n\tbackground-color: #4d4d4d;\n}\n#load-more {\n\tdisplay: block;\n\tmargin: 16px auto 24px;\n\tbackground-color: transparent;\n\tborder: 1px solid grey;\n\tborder-radius: 8px;\n\tcolor: grey;\n\tpadding: 8px 16px;\n\tcursor: pointer;\n\ttransition: all 0.1s ease-in-out;\n}\n#load-more:active {\n\tbackground-color: grey;\n\tcolor: white;\n}\n#fav-button {\n\tfont-size: 36px;\n\tposition: fixed;\n\tbottom: 16px;\n\tright: 16px;\n\tbackground-color: white;\n\tcolor: white;\n\tborder: 0;\n\tborder-radius: 50%;\n\twidth: 55px;\n\theight: 55px;\n\tcursor: pointer;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n}"],"sourceRoot":""}]);
